* {
  box-sizing: border-box;
}

body {
  color: #332bb2;
  background-color: #ccccd2;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  font-size: large;
  font-weight: bold;
  line-height: 1.5;
  display: flex;
}

p {
  margin: 0;
  padding-left: 96px;
  padding-right: 96px;
}

h2, h3 {
  margin-bottom: 8px;
}

.hidden {
  display: none;
}

.breed-select {
  color: #332bb2;
  border: 0;
  outline: 0;
  padding: 2px;
  font-weight: bold;
  box-shadow: 0 4px 12px rgba(0, 0, 0, .2), 0 16px 20px rgba(0, 0, 0, .2);
}

.cat-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 48px;
  display: flex;
}

.image {
  width: 600px;
}

.loader, .error {
  margin-top: 16px;
}

.error {
  color: #d7266a;
}

.loaders {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  animation: fill 1s ease-in infinite alternate;
  display: inline-block;
  position: relative;
  left: 48px;
}

.loaders:before, .loaders:after {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  animation: fill .9s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 20px;
}

.loaders:after {
  animation-duration: 1.1s;
  left: auto;
  right: 20px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

